.progress-wrapper {
    position: relative;
    width: 38px;
    text-align: center;
     font-family: sans-serif;
}

.progress-circle {
    position: relative;
    margin: auto;
}
.user-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    position: relative;
    z-index: 2;
}

.progress-ring {
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(110deg);
    /* Starts from ~20 degrees */
    width: 38px;
    height: 38px;
    z-index: 1;
}

.progress-ring circle {
    fill: none;
    stroke-width: 8;
}

.progress-bg {
    stroke: #e6e6e6;
}

.progress-bar {
    stroke: #4caf50;
    stroke-dasharray: 339.292;
    stroke-dashoffset: 0;
    transition: stroke-dashoffset 0.5s ease;
}

.progress-label {
    position: absolute;
    bottom: -5px;
    width: 100%;
    font-size: 8px;
    font-weight: bold;
    color: #4caf50;
    z-index: 2;
    background-color: rgba(255, 255, 255, 1);
    padding: 1px;
    border-radius: 5px;
}

.custom-notifiction .progress-wrapper {
    width: 80px;
}
.custom-notifiction .progress-circle,
.custom-notifiction .progress-ring {
    width: 80px;
    height: 80px;
}